export default {
    setAuthHeader(token = null) {
        if (token) {
            axios.defaults.headers.common['Authorization'] = `Token ${token}`;
        } else {
            delete axios.defaults.headers.common['Authorization']
        }
    },
    // login() {
    //     const params = {token: 'e40918de9f9f2c58350a98f08dc56ab85efbb4d0'}
    //     return axios.get("/api/user", {params})
    // }
    async login(params) {
        const user = await axios.post("/api/auth/", params)
            .then(({data}) => {
                return data});

        this.setAuthHeader(user.token);

        return axios.get(`/api/users/${user.user_id}/`)
            .then(({data}) => {
                data.token = user.token;
                return data;
            })
    },
    saveReglament(options) {
        const params = {
            name: options.name,
            label: options.parts.label.text,
            text1: options.parts.text1.text,
            text2: options.parts.text2.text,
            text3: options.parts.text3.text,
            text4: options.parts.text4.text,
            text5: options.parts.text5.text,
            do_approve: true
        };
        return axios.post(`/api/regulations/`, params)
    },
    revisions(options, reglamentId) {
        const params = [];
        options.forEach((item) => {
            if (item.comment) {
                
                params.push({
                    html_selection: item.html_selection,
                    report: item.comment,
                    secret_id: item.secret_id,
                    regulations_id: reglamentId,
                    default_text: item.default_text,
                    regulation_part:  item.tab === 0 ? 'label' : `text${item.tab}`
                })
            }
        });
        return axios.post(`/api/revisions/`, params)
    },
    changeReglament(options) {
        const params = {
            name: options.name,
            label: options.parts.label.text,
            text1: options.parts.text1.text,
            text2: options.parts.text2.text,
            text3: options.parts.text3.text,
            text4: options.parts.text4.text,
            text5: options.parts.text5.text,
        };

        return axios.patch(`/api/regulations/${options.id}/`, params)
    },
    approve(reglamentId, approver_id) {
        const params = {
            approver_id
        };

        return axios.patch(`/api/regulations/${reglamentId}/`, params)
    },
    async markRevisions(revisions) {
        await  revisions.forEach( (item) => {
            const params = {is_marked_solved: item.is_marked_solved};
            axios.patch(`/api/revisions/${item.id}/`, params )
        })
        return true;

        
    },


    getReglaments() {
        return axios.get(`/api/regulations/`);
    },
    getReglament(id) {
        return axios.get(`/api/regulations/${id}/`);
    },
    getRevisions(id) {
        return axios.get(`/api/revisions/`, {params: { regulationsId: id }});
    },
    getRevisionsByUser(id, revisorId) {
        return axios.get(`/api/revisions/`, {params: { regulationsId: id, created_by: revisorId }});
    }
}
