export const SET_CURRENT_USER = (state, user) => {
  state.currentUser = user;
};

export const LOGIN_FORM = (state, status) => {
  state.loginForm = status;
};

export const SET_LOADING = (state, payload) => {
  state.loading = payload;
};

export const SET_PROCESSING = (state, payload) => {
  state.processing = payload;
};
