<template>
    <div>
        <b-modal hide-footer hide-header-close no-close-on-backdrop
          no-close-on-esc id="modal-center"
          v-model="loginFormStatus" centered
          :title="currentUser.token ? 'Выход из системы' : 'Войти в систему'">
          <div v-if="!currentUser.token">
            <div>
              <label for="login-login">Логин</label>
              <input v-model="username" class="w-100" id="login-login" type="text">
            </div>
            <div class="mt-4">
              <label for="login-pass">Пароль</label>
              <input v-model="password" class="w-100" id="login-pass" type="password">
            </div>

            <div class="mt-5 mb-2 text-center">
              <button :disabled="!username || !password" @click="login" class="px-5">Войти</button>
              <div v-show="isError" class="mb-2 text-danger">
                Ошибка авторизации
              </div>
            </div>
            <div class="text-center mt-4 cursor-pointer">
              <img width="300" src="../assets/gosuslugi.png">
            </div>
          </div>
          <div v-else>
            <div class="text-center mt-4 h5">Вы уверены, что хотите выйти?</div>
            <div class="d-flex mt-5 justify-content-between">
              <button class="px-5" @click="changeLoginFormStatus(false)">Отмена</button>
              <button class="px-5" @click="logout">Да</button>
            </div>
          </div>
        </b-modal>
    </div>
</template>

<script>
import mainService from '../sevices/mainService'
import { mapActions, mapGetters } from 'vuex'

export default {
  name: 'LoginModal',
  data: () => ({
    username: "admin",
    password: "1234",
    isError: false
  }),
  computed: {
    ...mapGetters([
      'currentUser',
      'loginFormStatus'
    ])
  },
  methods: {
    ...mapActions({
      setUserDataToStore:'SET_CURRENT_USER',
      changeLoginFormStatus:'LOGIN_FORM'
    }),
    logout() {

      this.setUserDataToStore({});
      this.$router.push('/');
    },
    login() {
      this.isError = false;
      const params = {
        username: this.username,
        password: this.password
      }
      mainService.login(params)
        .then(this.setUserInf)
        // .catch(() => {
        //   this.setUserDataToStore({
        //     token: "123123"
        //   });
        // })
        .catch(this.showError)
    },
    setUserInf(data) {
      this.setUserDataToStore(data);
    },
    showError() {
      this.isError = true;
    }
  }
}
</script>

<style lang="scss" scoped>

</style>
