import mainService from '../sevices/mainService'

export const SET_CURRENT_USER = async ({ commit }, user) => {
  commit('SET_CURRENT_USER', user);
  localStorage.user = JSON.stringify(user);

  if (user.token) {
    mainService.setAuthHeader(user.token)
    commit('LOGIN_FORM', false);
  } else {
    commit('LOGIN_FORM', true);
  }
  commit('SET_CURRENT_USER', user);
};

export const LOGIN_FORM = ({ commit }, status) => {


  commit('LOGIN_FORM', status);
};

