<script>
import Header from './components/Header'
import { mapActions } from 'vuex'

export default {
  name: 'App',
  components: {
    Header
  },
  data: () => ({
    value: "",
    password: "",
    username: ""
  }),
  created() {
    this.getCurrentUser();
  },
  methods: {
    ...mapActions({
      setUserDataToStore:'SET_CURRENT_USER',
      changeLoginFormStatus:'LOGIN_FORM'
    }),
    getCurrentUser() {
      const currentUser = JSON.parse(localStorage.user);
      if (currentUser.token) {
        this.setUserDataToStore(currentUser);
      } else {
        this.changeLoginFormStatus(true);
      }
    }
  }
}
</script>

