import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

const routes = [
    {
      path: '/tasks',
      name: 'tasks',
      component: () => import('@/pages/Tasks'),
    },
    {
      path: '/reglaments',
      name: 'reglaments',
      component: () => import('@/pages/Reglaments'),
    },
    {
      path: '/reglament',
      name: 'reglament',
      component: () => import('@/pages/Reglament'),
    },
    {
      path: '/reglament/:id/:revisorId?/:revisionId?',
      name: 'reglamentEdit',
      props: true,
      component: () => import('@/pages/Reglament'),
    },
    {
      path: '/reglament/review/:id',
      name: 'reglamentReview',
      props: true,
      component: () => import('@/pages/ReglamentReview'),
    },
    {
      path: '/users',
      name: 'users',
      component: () => import('@/pages/Users'),
    },
    {
      path: '/task/:taskId',
      name: 'task',
      props: true,
      component: () => import('@/pages/Task'),
    },
    {
      path: '/revisions/:taskId/:revisorId',
      name: 'revisions',
      props: true,
      component: () => import('@/pages/Revisions'),
    },
    {
        path: '*',
        component: () => import('@/pages/E404'),
    }
]


const router = new VueRouter({
    mode: 'history',
    // base: "/",
    routes,
});

export default router;
